import React from "react";
import { useMutation } from "components/utils/queryClient";
import { Job } from "types";
import { Button, ButtonProps, Popconfirm, Tooltip } from "antd";
import RoleGuard from "components/standalone/role-guard";
import { StopFilled } from "@ant-design/icons";

type CancelJobButtonProps = {
  job: Job;
} & ButtonProps;

export default function CancelJobButton({
  job,
  ...props
}: CancelJobButtonProps) {
  const { mutateAsync: cancel, isLoading } = useMutation();

  function onClick() {
    cancel({
      key: `/job/${job?.id}`,
      action: "delete",
    });
  }

  return job?.status === 1 || job?.status === 4 ? (
    <RoleGuard requiredRoles={["Administrator"]}>
      <Popconfirm
        autoAdjustOverflow
        arrowPointAtCenter
        color="red"
        okText="Yes"
        cancelText="No"
        title="Are you sure you want to stop this job?"
        onConfirm={onClick}
      >
        <Tooltip title="Stop Job">
          <Button
            aria-label={`cancel job ${job?.id} button`}
            loading={isLoading}
            type="text"
            icon={<StopFilled />}
            {...props}
          />
        </Tooltip>
      </Popconfirm>
    </RoleGuard>
  ) : null;
}
