import { Job } from "../../types";
import { Space, Tooltip, Typography } from "antd";
import React from "react";
import { CalendarOutlined, HistoryOutlined } from "@ant-design/icons";
import { generateDateInfo, generateTimeInfo } from "components/utils/utils";
import dayjs from "dayjs";

export default function JobDateTime({ job, relativeTime = true }: { job: Job; relativeTime?: boolean; }) {
  return (
    <Space direction="horizontal" size="middle">
      <Space>
        <Typography.Text type="secondary" style={{ fontSize: 12 }}>
          <CalendarOutlined style={{ color: "inherit", fontSize: "inherit" }} />
        </Typography.Text>
        <Typography.Text type="secondary" style={{ fontSize: 12 }}>
          <Tooltip title={dayjs(job?.startTime).toString()}>
            {generateDateInfo(job, relativeTime)}
          </Tooltip>
        </Typography.Text>
      </Space>
      <Space>
        <Typography.Text type="secondary" style={{ fontSize: 12 }}>
          <HistoryOutlined style={{ color: "inherit", fontSize: "inherit" }} />
        </Typography.Text>
        <Typography.Text type="secondary" style={{ fontSize: 12 }}>
          {generateTimeInfo(job)}
        </Typography.Text>
      </Space>
    </Space>
  );
}
