import React from "react";
import { Button, ButtonProps, Tag, Tooltip } from "antd";
import { useNavigate, useLocation } from "react-router-dom";
import { Job } from "types";
import RoleGuard from "components/standalone/role-guard";
import { toRelativeUrl } from "components/utils/utils";

type ViewScriptButtonProps = {
  job: Job;
} & ButtonProps;

export default function ViewScriptButton({
  job,
  ...props
}: ViewScriptButtonProps) {
  const navigate = useNavigate();
  const location = useLocation();

  return (
    <RoleGuard requiredRoles={["Administrator"]}>
      <Tooltip title={job?.isScriptDeleted ? "Script deleted" : "Go to script"}>
        <Button
          type="text"
          {...props}
          disabled={job?.isScriptDeleted}
          aria-label={`view script ${job?.scriptFullPath} button`}
          onClick={() =>
            navigate(toRelativeUrl(`/admin/automation/scripts/${job?.scriptFullPath}`), {
              state: {
                from: location?.pathname,
              },
            })
          }
        >
          <Tag>
            {job?.scriptFullPath}
          </Tag>

        </Button>
      </Tooltip>
    </RoleGuard>
  );
}
